form{
    width: 250px;
    background: #16222a;
    background: -webkit-linear-gradient(to right, #3A6073, #16222A);  
    background: linear-gradient(to right, #3A6073, #16222A); 
    margin: auto;
    margin-top: 150px;
    padding: 20px;
    border-radius: 20px;
}

.field{
    margin: 8px 35px;
    padding: 8px;
    border: none;
    outline: none;
}

.btn{
    margin: 8px 90px;
    border-radius: 10px;
    border: none;
    padding: 10px;
}
.btn:hover{
    background-color: black;
    color: white;
}