form.login {
    width: 400px;
    margin: auto;
    margin-top: 150px;
    background: #283c86;
    background: -webkit-linear-gradient(to left, #45a247, #283c86);
    background: linear-gradient(to left, #45a247, #283c86);
    border: 1px solid;
    border-radius: 20px;
  }
  .login-input{
    margin: 30px 120px;
  }
  .login-input input{
    padding: 8px;
    border: none;
    outline: none;
  }
  .login-submit{
    margin: 30px 165px;
  }
  .login-button{
    border-radius: 9px;
    padding: 10px 15px;
    cursor: pointer;
  }
  .login-button:hover{
    background-color: black;
    color: white ;
  }
  .login-heading{
    position: relative;
    margin-top: 20px;
    left: 120px;
    color: white;
  }