form.signup{
    width: 400px;
    margin: auto;
    margin-top: 120px;
    background: #F0C27B;  
    background: -webkit-linear-gradient(to left, #4B1248, #F0C27B);
    background: linear-gradient(to left, #4B1248, #F0C27B); 
    border: 1px solid;
    border-radius: 20px;
}

.signup-heading{
    margin: 20px 99px;
    color: white;
}

.signup-input{
    margin: 25px 110px;
}

.signup-submit{
    margin: 4% 40%;
}
.signup-input input{
    padding: 4%;
    border: none;
    outline: none;
    /* background: transparent;
    border: none;
    outline: none;
    border-bottom: 1px solid rgba(255, 255, 255, 0.35); */
}
.signup-button{
    border-radius: 9px;
    padding: 10px 15px;
    cursor: pointer; 
    
}
.signup-button:hover{
    background-color: black;
    color: white;
}

  